$primaryColor: #146EF5;

.bPlSortablePanelItem {
	.itemsPanelHeader {
		border: 1px solid #ccc;
		display: flex;
		align-items: center;
		height: 40px;
		margin: 0px;

		.gripIcon {
			cursor: grab;
			margin: 0px;
			display: grid;
			padding: 0 10px;
			height: 100%;

			svg {
				width: 16px;
				height: 16px;
				place-self: center;
			}
		}

		.panel-header-title {
			width: 100%;
			height: 100%;
			display: flex;
			align-items: center;
			justify-content: space-between;

			.repeater-panel-title {
				cursor: pointer;
				margin: 0px;
				height: 100%;
				// width: 100%;
				width: 125px;
				display: inline-block;
				padding-left: 5px;
				transition: background-color 0.2s ease-in-out;
				padding-top: 10px;
				white-space: nowrap;
				overflow: hidden;
				text-overflow: ellipsis;

				&:hover {
					background-color: #ccc;
				}
			}

			.itemAction {
				display: flex;
				height: 100%;

				>div {
					border-left: 1px solid #ccc;
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					width: 35px;
					cursor: pointer;
					font-size: 17px;

					svg {
						width: 18px;
						height: 18px;
					}

					&:hover {
						background-color: #ccc;
					}
				}
			}
		}
	}

	.itemsPanelBody {
		padding: 10px;
		border: 1px solid #ccc;
		border-top: 0px;
		transition: all 0.5s ease;

		&.hidden {
			height: 0;
		}
	}
}